'use client'

import { MoreVertical, ShoppingCart, BarChart3, DollarSign, Clock, Plus } from 'lucide-react'
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'

const earningData = [
  { category: 'Orders', value: 28000 },
  { category: 'Sales', value: 45000 },
  { category: 'Profit', value: 38000 },
  { category: 'Income', value: 30000 },
  { category: 'Revenue', value: 28000 },
]

export function EarningReports() {
  return (
    <div className="bg-white dark:bg-gray-950 border-b border-slate-700 rounded-lg p-6   shadow-sm lg:col-span-2 ">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h3 className="text-lg font-bold text-gray-900 dark:text-white">Earning Reports</h3>
          <p className="text-gray-500 text-sm dark:text-white">Yearly Earnings Overview</p>
        </div>
        <button className="text-gray-400 dark:text-white hover:text-gray-600">
          <MoreVertical size={20} />
        </button>
      </div>

      {/* Tab Buttons */}
      <div className="flex gap-2 mb-6 pb-4 border-b border-gray-100 dark:text-white overflow-x-auto">
        <button className="px-4 py-2 rounded-lg border-2 border-purple-500 text-purple-600 font-medium text-sm flex items-center gap-2 whitespace-nowrap hover:bg-purple-50 transition-colors">
          <ShoppingCart size={18} /> Orders
        </button>
        <button className="px-4 py-2 rounded-lg border border-gray-200 text-gray-600 dark:text-white font-medium text-sm flex items-center gap-2 whitespace-nowrap hover:bg-gray-50 transition-colors">
          <BarChart3 size={18} /> Sales
        </button>
        <button className="px-4 py-2 rounded-lg border border-gray-200 text-gray-600 dark:text-white font-medium text-sm flex items-center gap-2 whitespace-nowrap hover:bg-gray-50 transition-colors">
          <DollarSign size={18} /> Profit
        </button>
        <button className="px-4 py-2 rounded-lg border border-gray-200 text-gray-600 dark:text-white font-medium text-sm flex items-center gap-2 whitespace-nowrap hover:bg-gray-50 transition-colors">
          <Clock size={18} /> Income
        </button>
        <button className="px-4 py-2 rounded-lg border border-gray-200 text-gray-600 dark:text-white font-medium text-sm flex items-center justify-center whitespace-nowrap hover:bg-gray-50 transition-colors">
          <Plus size={18} />
        </button>
      </div>

      {/* Chart */}
      <ResponsiveContainer width="100%" height={300}>
        <BarChart data={earningData} margin={{ top: 20, right: 30, left: 0, bottom: 20 }}>
          <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" vertical={false} />
          <XAxis dataKey="category" stroke="#9ca3af" style={{ fontSize: '12px' }} />
          <YAxis stroke="#9ca3af" style={{ fontSize: '12px' }} />
          <Tooltip contentStyle={{ backgroundColor: '#fff', border: '1px solid #e5e7eb', borderRadius: '8px' }} />
          <Bar dataKey="value" fill="#8b5cf6" radius={[8, 8, 0, 0]} />
        </BarChart>
      </ResponsiveContainer>
    </div>
  )
}
